import { socializedComments } from './socialized_comments'
import { analysisPlatform } from './analysis_platform'
import { onlineProgrammingPlatform } from './online_programming_platform'
import { cloudPlatform } from './cloud_platform'
import { interfaceService } from './interface_service'

const serviceWebList = [
  {
    'name': '社会化评论',
    'title': 'socialized comments',
    'ico': 'icon-pinglun',
    'cards': socializedComments
  },
  {
    'name': '统计分析平台',
    'title': 'analysis platform',
    'ico': 'icon-tongji',
    'cards': analysisPlatform
  },
  {
    'name': '图床平台',
    'title': 'cloud platform',
    'ico': 'icon-yun',
    'cards': cloudPlatform
  },
  {
    'name': '在线编程平台',
    'title': 'online programming platform',
    'ico': 'icon-programmingwindo',
    'cards': onlineProgrammingPlatform
  },
  {
    'name': '数据接口服务',
    'title': 'interface service',
    'ico': 'icon-APIwendang',
    'cards': interfaceService
  }
]
export { serviceWebList }



