import { versionControlSystem } from './version_control_system'
import { textEditor } from './text_editor'
import { httpProxy } from './http_proxy'

const developToolList = [
  {
    'name': '版本控制',
    'title': 'version control system',
    'ico': 'icon-git',
    'cards': versionControlSystem
  },
  {
    'name': '文本编辑器',
    'title': 'text editor',
    'ico': 'icon-sublimetext',
    'cards': textEditor
  },
  {
    'name': 'HTTP代理',
    'title': 'http proxy',
    'ico': 'icon-http',
    'cards': httpProxy
  }
]
export { developToolList }



