<template>
  <div class="side-nav" :class="{show: isNavShow === true, hide: isNavShow === false}">
    <!-- info -->
    <header-info class="pc-header"></header-info>
    <!-- nav -->
    <div class="nav-box">
      <div class="level1-box"
        v-for="nav_one in nav"
        :key="nav_one.name">
        <a class="level1-info"
          :class="{active: nav_one.name === path}"
          @click="navOneClick(nav_one.name)">
          {{nav_one.name}}<i :class="nav_one.ico"></i>
        </a>
        <div class="level2-box"
          :class="{active: nav_one.name === path}">
          <a class="level2-info"
            :class="{active: nav_two.name === activedHash}"
            @click="navTwoClick(nav_two.name)"
            :title="nav_two.title"
            v-for="nav_two in nav_one.list"
            :key="nav_two.name">
            {{nav_two.name}}<i :class="nav_two.ico"></i>
          </a>
        </div>
      </div>
    </div>
    <!-- contact -->
    <!-- <div class="contact"></div> -->
    <!-- info -->
    <!-- <div>Copyright © 2017-2018  前端工程师导航</div> -->
  </div>
</template>

<script>
import nav from 'api/index'
import HeaderInfo from '../header-info/header-info'
export default {
  created () {
    // 设置网站载入时初始的默认显示内容
    var initName = nav[0].name
    this.path = initName
    this.$emit('nav-one-click', initName)
  },
  methods: {
    navOneClick (path) {
      this.path = path
      this.$emit('nav-one-click', path)
    },
    navTwoClick (hash) {
      // this.hash = name
      this.$emit('nav-two-click', hash)
    }
    // ,
    // getUrl () {
    //   this.path = decodeURIComponent(this.$route.path)
    //   this.hash = decodeURIComponent(this.$route.hash)
    // }
  },
  // watch: {
  //   // 如果路由有变化，会再次执行该方法
  //   '$route': 'getUrl'
  // },
  props: ['activedHash', 'isNavShow'],
  data () {
    return {
      // nav是原始数据
      nav: nav,
      // path代表一级导航
      path: ''
      // hash代表二级导航
      // hash: ''
    }
  },
  components: {
    HeaderInfo
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
@import "~assets/sass/variable"
@import "~assets/sass/icon"
@import "~assets/sass/mixin"
.side-nav
  width: $nav-width
  background-color: $nav-bg
  padding-top: 60px

  // fixed定位
  position: fixed
  top: 0
  left: 0
  bottom: 0
  z-index: 3
  // flex布局
  // display: flex
  // flex-direction: column

  transition: transform 430ms cubic-bezier(0.3,0,0,1)
  &.show
    transform: translateX(0)
  &.hide
    transform: translateX(-$nav-width)

  // PC端 默认显示，极小不显示
  .pc-header
    margin-bottom: 20px
    @include sc-xs()
      display: none

  .nav-box
    .level1-box
      .level1-info
        cursor: pointer
        // 把a元素由行内元素转换为块级元素，扩大可点击区域
        display: block
        height: $nav-level1-info-height
        line-height: $nav-level1-info-height

        color: $nav-level1-color
        font-size: $font-size-medium
        font-weight: bold
        text-decoration: none
        text-align: right
        padding-right: 64px
        >i
          margin-left: 10px
        &.active
          background-color: $nav-level1-active-bg
          color: $nav-level1-active-color
        @include sc-no-xs()
          &:hover
            background-color: $nav-level1-active-bg
            color: $nav-level1-active-color
      .level2-box
        display: none
        &.active
          display: block
        // max-height: 7*$nav-level2-info-height
        // overflow-y: auto
        .level2-info
          cursor: pointer
          display: block
          height: $nav-level2-info-height
          line-height: $nav-level2-info-height

          color: $nav-level2-color
          font-size: $font-size-small
          text-decoration: none
          text-align: right
          padding-right: 64px
          >i
            margin-left: 10px
          &.active
            background-color: $nav-level2-active-bg
            color: $nav-level2-active-color
          @include sc-no-xs()
            &:hover
              background-color: $nav-level2-active-bg
              color: $nav-level2-active-color
</style>



