<template>
  <div class="mb-header">
    <div class="toggle-nav" @click="toggleHandler" :class="{'show': addShowClass === true}">
      <span></span>
      <span></span>
      <span></span>
    </div>
    <header-info></header-info>
    <mb-mask :is-mask-show="isMaskShow"></mb-mask>
  </div>
</template>

<script>
import HeaderInfo from '../header-info/header-info'
import MbMask from './mb-mask/mb-mask'
export default {
  watch: {
    isNavShow: function (v) {
      this.isMaskShow = v
      this.addShowClass = v

      // var body = document.querySelector('body')
      // if (v === true) {
      //   body.style.overflow = 'hidden'
      // } else {
      //   body.style.overflow = ''
      // }
    }
  },
  props: ['isNavShow'],
  data () {
    return {
      addShowClass: false,
      isMaskShow: false
    }
  },
  methods: {
    toggleHandler () {
      if (this.isNavShow === false) {
        this.$emit('toggel-nav-status', true)
      } else {
        this.$emit('toggel-nav-status', false)
      }
    }
  },
  components: {
    HeaderInfo,
    MbMask
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
@import "~assets/sass/mixin"
@import "~assets/sass/variable"
// MB端 默认不显示，极小显示
.mb-header
  display: none
  @include sc-xs()
    display: block

  .toggle-nav
    @include extend-click()
    left: 20px
    top: 20px
    position: fixed
    z-index: 4
    &.show
      > span
        // 第一个元素，最右侧为旋转基点，逆时针旋转45°
        &:nth-child(1)
          opacity: 1
          transform: rotate(-45deg)
        &:nth-child(2)
          opacity: 0
        // 第三个元素，最右侧为旋转基点，顺时针旋转45°
        &:nth-child(3)
          opacity: 1
          transform: rotate(45deg)
    > span
      display: block
      background: #fff
      width: 20px
      height: 1px

      margin: 0 auto 6px
      &:last-child
        margin-bottom: 5px

      // 设置 旋转元素的基点位置
      // transform-origin: x-axis y-axis z-axis
      transform-origin: 20px
      transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0), background 0.5s cubic-bezier(0.77,0.2,0.05,1.0), opacity 0.55s ease
</style>



