import { imgBaseUrl } from '../config'
const testFramework = [
  {
    'card_id': '',
    'logo_url': imgBaseUrl + 'mochajs.png',
    'bg_cl': '#fff',
    'favour': '',
    'watch': '',
    'c_href': '',
    'href': 'http://mochajs.org/',
    'name': 'mocha',
    'describe': 'Mocha is a feature-rich JavaScript test framework running on Node.js and in the browser, making asynchronous testing simple and fun. '
  },
  {
    'card_id': '',
    'logo_url': imgBaseUrl + 'jasmine.png',
    'bg_cl': '#fff',
    'favour': '',
    'watch': '',
    'c_href': '',
    'href': 'https://jasmine.github.io/edge/introduction.html',
    'name': 'Jasmine',
    'describe': 'Jasmine is a Behavior Driven Development testing framework for JavaScript. '
  }
]
export { testFramework }



