import { htmlCssList } from './htmlcss/index'
import { javascriptList } from './javascript/index'
import { nodejsList } from './nodejs/index'
import { buildToolList } from './buildtool/index'
import { developToolList } from './developtool/index'
import { contentWebList } from './contentweb/index'
import { serviceWebList } from './serviceweb/index'

const nav = [
  {
    'name': 'HTML & CSS',
    'ico': 'icon-msnui-website',
    'list': htmlCssList
  },
  {
    'name': 'JavaScript',
    'ico': 'icon-javascript',
    'list': javascriptList
  },
  {
    'name': 'Nodejs',
    'ico': 'icon-nodejs',
    'list': nodejsList
  },
  {
    'name': '前端构建工具',
    'ico': 'icon-gongjuxiang',
    'list': buildToolList
  },
  {
    'name': '项目开发工具',
    'ico': 'icon-kaifazhong',
    'list': developToolList
  },
  {
    'name': '内容类网站',
    'ico': 'icon-zhishizhongxinoff',
    'list': contentWebList
  },
  {
    'name': '服务类网站',
    'ico': 'icon-fuwu',
    'list': serviceWebList
  }
]

export default nav



