<template>
  <div class="card">
    <!-- 缩略图 -->
    <div class="thumbnail">
      <!-- logo图 -->
      <!-- <img class='image' :src="card.logo_url" :alt="card.describe" height="100"> -->
      <div class="image" :style="'background-image: url('+card.logo_url+');background-color:'+card.bg_cl">
        <a :href="card.href||card.c_href" :title="card.href||card.c_href" target="_blank"></a>
      </div>
      <!-- 统计数据：点赞和观看 -->
      <!-- <div class="statistic">
        <div class="favour"><i class="icon-xihuan"></i>{{card.favour}}</div>
        <div class="watch"><i class="icon-liulan"></i>{{card.watch}}</div>
      </div> -->
      <!-- 链接地址：官网和中文网站 -->
      <div class="link">
        <a v-if="card.href" :href="card.href" target="_blank"><i class="icon-yingwen"></i></a>
        <a v-if="card.c_href" :href="card.c_href" target="_blank"><i class="icon-zhongwen"></i></a>
      </div>
    </div>
    <!-- 文字内容：名称和描述 -->
    <div class="character">
      <a :href="card.href||card.c_href" :title="card.href||card.c_href" class="name" target="_blank">{{card.name}}</a>
      <p class="describe" :title="card.describe">{{card.describe}}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: ['card']
}
</script>

<style scoped lang="sass">
@import "~assets/sass/mixin"
@import "~assets/sass/variable"
@import "~assets/sass/icon"
.card
  margin-bottom: 20px
  .thumbnail:hover .statistic, .thumbnail:hover .link
    height: $card-tip-height
  .thumbnail
    height: $card-height
    position: relative
    .image
      &>a
        display: block
        height: 100%
      // overflow: hidden
      // margin: 0 auto
      height: $card-height // <==> height: 100%
      background-position: center
      background-repeat: no-repeat
      // 背景色，在图片之下
      background-color: $card-default-bg-color
    .statistic, .link
      // visibility: hidden
      transition: height .1s ease-out
      position: absolute
      width: 100%
      height: 0
      overflow: hidden
      background: rgba(0, 0, 0, 0.7)
    .statistic
      top: 0
      display: flex
      justify-content: flex-end
      align-items: center
      .favour
        color: $favour-color
        font-size: $font-size-medium
      .watch
        color: $watch-color
        font-size: $font-size-medium
    .link
      bottom: 0
      padding-left: 4px
      display: flex
      justify-content: flex-start
      align-items: center
      &>a
        text-decoration: none
        margin-left: 3px
        color: $link-color
        font-size: $font-size-medium
  .character
    display: flex
    flex-direction: column
    .name
      text-decoration: none
      font-size: $font-size-medium
      font-weight: $name-weight
      color: $name-color
      // 单行文本溢出省略号
      @include ellipsis()
      height: $card-name-height
      line-height: $card-name-height
    .describe
      font-size: $font-size-small
      color: $describe-color
      // 多行文本溢出省略号
      @include multiline-ellipsis()
      height: $card-describe-height * 2
      line-height: $card-describe-height
</style>



