import { nodejsPackageManager } from './nodejs_package_manager'
import { nodejsWebFrame } from './nodejs_web_frame'
import { projectDeployment } from './project_deployment'
import { templateEngine } from './template_engine'
import { moreNodejsPackage } from './more_nodejs_package'

const nodejsList = [
  {
    'name': 'nodejs包管理',
    'title': 'nodejs package manager',
    'ico': 'icon--npm',
    'cards': nodejsPackageManager
  },
  {
    'name': 'web框架',
    'title': 'web frame for nodejs',
    'ico': 'icon-web',
    'cards': nodejsWebFrame
  },
  {
    'name': '模板引擎',
    'title': 'template engine',
    'ico': 'icon-moban',
    'cards': templateEngine
  },
  {
    'name': '项目部署',
    'title': 'project deployment',
    'ico': 'icon-bushu',
    'cards': projectDeployment
  },
  {
    'name': '更多nodejs包',
    'title': 'more nodejs package',
    'ico': 'icon-package',
    'cards': moreNodejsPackage
  }
]
export { nodejsList }



