import { team } from './team'
import { specialist } from './specialist'
import { community } from './community'
import { docment } from './docment'
import { learnOnline } from './learn_online'
import { conference } from './conference'

const contentWebList = [
  {
    'name': '团队组织',
    'title': 'team',
    'ico': 'icon-team',
    'cards': team
  },
  {
    'name': '个人大牛',
    'title': 'specialist',
    'ico': 'icon-geren',
    'cards': specialist
  },
  {
    'name': '开发社区',
    'title': 'community',
    'ico': 'icon-community',
    'cards': community
  },
  {
    'name': '参考文档',
    'title': 'docment',
    'ico': 'icon-library',
    'cards': docment
  },
  {
    'name': '在线学习',
    'title': 'learn_online',
    'ico': 'icon-online',
    'cards': learnOnline
  },
  {
    'name': '开发者大会',
    'title': 'conference',
    'ico': 'icon-huiyikaihuitaolun',
    'cards': conference
  }
]
export { contentWebList }



