<template>
  <div class="mb-mask" :class="{show: isMaskShow === true}"></div>
</template>

<script>
export default {
  props: {
    isMaskShow: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
@import "~assets/sass/variable"
.mb-mask
  background: rgba(0,0,0,0.75)
  height: 100%
  width: 100%
  position: fixed
  // top: 60px
  // bottom: 0
  // left: 0
  // right: 0
  z-index: 1
  display: none
  &.show
    display: block
</style>



