import { taskExecutor } from './task_executor'
import { modulePacker } from './module_packer'
import { testFramework } from './test_framework'
import { scaffoldingTools } from './scaffolding_tools'
import { syntaxCheck } from './syntax_check'
import { otherTools } from './other_tools'

const buildToolList = [
  {
    'name': '任务执行',
    'title': 'task executor',
    'ico': 'icon-gulp',
    'cards': taskExecutor
  },
  {
    'name': '模块打包',
    'title': 'module packer',
    'ico': 'icon-webpack',
    'cards': modulePacker
  },
  {
    'name': '脚手架',
    'title': 'scaffolding tools',
    'ico': 'icon-build',
    'cards': scaffoldingTools
  },
  {
    'name': '语法检查',
    'title': 'syntax check',
    'ico': 'icon-ceshi1',
    'cards': syntaxCheck
  },
  {
    'name': '测试框架',
    'title': 'test framework',
    'ico': 'icon-ceshi',
    'cards': testFramework
  },
  {
    'name': '其他工具',
    'title': 'other tools',
    'ico': 'icon-icon--',
    'cards': otherTools
  }
]
export { buildToolList }



