<template>
  <div class="home">
    <mb-header
      @toggel-nav-status="toggelNavStatus"
      :is-nav-show="isNavShow">
    </mb-header>
    <side-nav
      @nav-one-click="navOneClick"
      @nav-two-click="navTwoClick"
      :actived-hash="activedHash"
      :is-nav-show="isNavShow">
    </side-nav>
    <main-cont
     :path="path"
     :clicked-hash="clickedHash"
     @change-actived-hash="changeActivedHash">
    </main-cont>
  </div>
</template>

<script>
import MbHeader from './mb-header/mb-header'
import SideNav from './side-nav/side-nav'
import MainCont from './main-cont/main-cont'

export default {
  created () {
    window.onresize = init
    window.onload = init

    var vm = this
    var temp
    function init () {
      var winWidth = document.body.clientWidth
      if (temp !== winWidth) {
        temp = winWidth
        // console.log(`网页可见区域的宽度 ${winWidth}`)
        if (winWidth < 768) {
          vm.isNavShow = false
        } else {
          vm.isNavShow = true
        }
        // console.log(`isNavShow的值为 ${vm.isNavShow}`)
      }
    }
  },
  data () {
    return {
      path: '',
      activedHash: '',
      clickedHash: '',
      isNavShow: true
    }
  },
  methods: {
    toggelNavStatus (flag) {
      this.isNavShow = flag
    },
    changeActivedHash (hash) {
      this.activedHash = hash
    },
    navOneClick (path) {
      // console.log(name)
      this.path = path
      this.clickedHash = new Date().getTime().toString().slice(5) + ''
    },
    navTwoClick (hash) {
      // console.log(name)
      this.activedHash = hash
      this.clickedHash = new Date().getTime().toString().slice(5) + hash
    }
  },
  // created () {
  //   var baseTitle = 'FEE-WEB前端导航'
  //   window.document.title = baseTitle
  // },
  name: 'App',
  components: {
    SideNav,
    MainCont,
    MbHeader
  }
}
</script>

<style lang="sass">
// @import "~assets/sass/reset"
// @import "~assets/sass/base"
</style>



