import { javascriptLibrary } from './javascript_library'
import { visualizedLibrary } from './visualized_library'
import { moreJavascriptSyntax } from './more_javascript_syntax'
import { javascriptFrame } from './javascript_frame'
import { mobileApp } from './mobile_app'
import { moreJavascriptLibrary } from './more_javascript_library'

const javascriptList = [
  {
    'name': 'JavaScript库',
    'title': 'JavaScript library',
    'ico': 'icon-jquery',
    'cards': javascriptLibrary
  },
  {
    'name': '可视化库',
    'title': 'Visualized library',
    'ico': 'icon-echart',
    'cards': visualizedLibrary
  },
  {
    'name': 'JavaScript框架',
    'title': 'JavaScript frame',
    'ico': 'icon-react',
    'cards': javascriptFrame
  },
  {
    'name': '移动开发',
    'title': 'mobile app',
    'ico': 'icon-ionic',
    'cards': mobileApp
  },
  {
    'name': '更多JavaScript语法',
    'title': 'more JavaScript syntax',
    'ico': 'icon-coffee',
    'cards': moreJavascriptSyntax
  },
  {
    'name': '更多JavaScript库',
    'title': 'more JavaScript library',
    'ico': 'icon-yingyongkuangjiachajian',
    'cards': moreJavascriptLibrary
  }
]
export { javascriptList }



