<template>
  <div class="main-cont">
    <div class="main">
      <section class="panel" v-for="(item,index) in contData" :key="index" :id="item.name">
        <header class="title">
          <i :class="item.ico"></i>
          <span class="title-name">{{item.name}}</span>
          <span class="title-desc">&nbsp;|&nbsp;{{item.title}}</span>
        </header>
        <!-- <article class="body row">
          <card :card="card" class="col-sm-6 col-md-4 col-lg-3" v-for="(card,cardIndex) in item.cards" :key="cardIndex"></card>
        </article> -->
        <article class="body">
          <div class="row">
            <card :card="card" class="col-sm-6 col-md-4 col-lg-3" v-for="(card,cardIndex) in item.cards" :key="cardIndex"></card>
            <!-- <div class="col-sm-6 col-md-4 col-lg-3" v-for="(card,cardIndex) in item.cards" :key="cardIndex">
              <card :card="card"></card>
            </div> -->
          </div>
        </article>
      </section>
    </div>
  </div>
</template>

<script>
import Card from './card/card'
import nav from 'api/index'
export default {
  props: ['path', 'clickedHash'],
  methods: {
    // 获取元素相对于文档的top值
    _getElTop (el) {
      // 使用 el.offsetTop 返回当前元素相对于其 offsetParent 元素的顶部的距离。
      // https://developer.mozilla.org/zh-CN/docs/Web/API/HTMLElement/offsetTop
      var top = el.offsetTop
      var current = el.offsetParent
      while (current !== null) {
        // console.log('当前元素', current)
        top += current.offsetTop
        current = current.offsetParent
      }
      return top
    },
    // 二级导航变化时，滚动到指定hash的函数
    scroll () {
      // 移除滚动监听
      this._delScrollListener()
      console.log('移除滚动监听')
      // 获取hash值，前8位是随机字符串
      var hash = this.clickedHash.slice(8)
      // 默认目标高度为0，（在hash值为''时）
      var targetTop = 0
      // hash值不为''时，目标高度为元素高度-20
      if (hash !== '') {
        var el = document.getElementById(hash)
        // 1 获取元素相对于文档的top值
        var elTop = this._getElTop(el)
        // 设置目标高度
        targetTop = elTop - 20
      }

      // 获取滚动条位置
      // chrome -> document.body.scrollTop
      // firefox -> document.documentElement.scrollTop
      var currentTop = document.body.scrollTop || document.documentElement.scrollTop
      // console.log('currentTop', currentTop)

      var stepLong = 16
      var step
      if (currentTop > targetTop) {
        step = (currentTop - targetTop) / stepLong
        requestAnimationFrame(scrollUp)
      } else {
        step = (targetTop - currentTop) / stepLong
        requestAnimationFrame(scrollDown)
      }

      // 2 滚动到指定位置
      // window.scrollTo(0, targetTop)

      var that = this
      // 向上滚动
      function scrollUp () {
        currentTop = currentTop - step
        window.scrollTo(0, currentTop)
        console.log('向上滚动至', currentTop, '目标位置', targetTop)
        if (currentTop > targetTop) {
          requestAnimationFrame(scrollUp)
          console.log('执行滚动动画scrollUp')
        } else {
          requestAnimationFrame(that._addScrollListener)
          console.log('添加滚动监听')
        }
      }
      // 向下滚动
      function scrollDown () {
        currentTop = currentTop + step
        window.scrollTo(0, currentTop)
        console.log('向下滚动至', currentTop, '目标位置', targetTop)
        if (currentTop < targetTop) {
          requestAnimationFrame(scrollDown)
          console.log('执行滚动动画scrollDown')
        } else {
          requestAnimationFrame(that._addScrollListener)
          console.log('添加滚动监听')
        }
      }
    },
    // 一级导航变化时，获取数据
    fetchData () {
      var path = this.path
      // console.log(path)

      for (var i = 0; i < nav.length; i++) {
        if (path === nav[i].name) {
          this.contData = nav[i].list
        }
      }
      // console.log('获取的数据为：', this.contData)
    },
    // 点击二级导航scroll函数开始执行时，移除页面滚动事件处理函数
    _delScrollListener () {
      // 移除事件处理函数
      window.removeEventListener('scroll', this._scrollHandler)
    },
    // 点击二级导航scroll函数执行完之后，添加页面滚动事件处理函数
    _addScrollListener () {
      // 为当前页面的页面滚动事件添加事件处理函数
      // window.onscroll
      window.addEventListener('scroll', this._scrollHandler)
    },
    // 页面滚动事件处理函数
    _scrollHandler (e) {
      var currentTop = document.body.scrollTop || document.documentElement.scrollTop
      console.log('页面滚动高度为：', currentTop)
      // console.log('页面滚动时的数据为：', this.contData)

      var data = this.contData
      for (var i = 0; i < data.length - 1; i++) {
        var name = data[i].name
        var el = document.getElementById(name)
        var elTop = this._getElTop(el)

        var nextName = data[i + 1].name
        var nextEl = document.getElementById(nextName)
        var nextElTop = this._getElTop(nextEl)
        if (currentTop >= elTop - 20 && currentTop < nextElTop - 20) {
          // console.log('当前name', name)
          this.$emit('change-actived-hash', name)
        }
      }
    }
  },
  watch: {
    // 如果变量有变化，会再次执行该方法
    'path': 'fetchData',
    'clickedHash': 'scroll'
  },
  data () {
    return {
      contData: []
    }
  },
  components: {
    Card
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
@import "~assets/sass/mixin"
@import "~assets/sass/variable"
@import "~assets/sass/grid"
@import "~assets/sass/icon"
.main-cont
  margin-left: $nav-width
  @include sc-xs()
    margin-left: 0
    margin-top: $mb-header-height
    background: $panel-bg
  padding: 20px 0
  background: $main-cont-bg
  .main
    margin: 0 auto
    // background: #f3f6f8
    // lg >=1300 1270 - 230 = 1040
    @include sc-lg()
      width: $sc-lg-width - $nav-width
    // md >=992 970 - 230 = 740
    @include sc-md()
      width: $sc-md-width - $nav-width
    // sm >=768 750 - 230 = 520
    @include sc-sm()
      width: $sc-sm-width - $nav-width
    // <768
    @include sc-xs()
      width: 100%
      // margin: 0
    .panel
      background-color: $panel-bg
      border-radius: 6px
      padding: 20px 20px
      margin-bottom: 40px
      @include sc-xs()
        padding: 0
      .title
        margin-bottom: 20px
        box-shadow: $panel-shodaw
        display: flex
        flex-direction: row
        line-height: $panel-title-height
        height: $panel-title-height
        &:hover
          & > .title-desc
            display: block
        >i
          font-size: $font-size-medium-x
          margin-right: 10px
        .title-name
          font-size: $font-size-small
          font-weight: $panel-weight
          color: $panel-color

        .title-desc
          display: none
          font-size: $font-size-small
          font-weight: $panel-weight
          color: $panel-color

      // 闭合浮动
      // .body>div
      //   &::after
      //     @include clearfix
</style>



