import { cssReset } from './css_reset'
import { cssAnimation } from './css_animation'
import { cssFramework } from './css_framework'
import { cssPreProcessor } from './css_pre_processor'
import { fontIcon } from './font_icon'
import { frontEndFramework } from './front_end_framework'
import { blogFramework } from './blog_framework'
import { presentationFramework } from './presentation_framework'

const htmlCssList = [
  {
    'name': '浏览器样式重置',
    'title': 'css reset',
    'ico': 'icon-CSSyangshi',
    'cards': cssReset
  },
  {
    'name': 'css动画库',
    'title': 'css animation',
    'ico': 'icon-Dchanpindonghuayanshi',
    'cards': cssAnimation
  },
  {
    'name': 'css预编译',
    'title': 'css pre-processor',
    'ico': 'icon-sass',
    'cards': cssPreProcessor
  },
  {
    'name': '字体图标',
    'title': 'font icon',
    'ico': 'icon-ico-presetartillustr',
    'cards': fontIcon
  },
  {
    'name': 'css框架',
    'title': 'css framework',
    'ico': 'icon-iframe',
    'cards': cssFramework
  },
  {
    'name': '前端框架',
    'title': 'front-end framework',
    'ico': 'icon-bootstrap',
    'cards': frontEndFramework
  },
  {
    'name': '博客框架',
    'title': 'blog framework',
    'ico': 'icon-wordpress',
    'cards': blogFramework
  },
  {
    'name': '演示框架',
    'title': 'presentation framework',
    'ico': 'icon-jiaohuyanshidonghua',
    'cards': presentationFramework
  }
]
export { htmlCssList }



